
.logo {
  font-size: 80px;
  color: red;
  background: white;
  width: 130px;
  height: 130px;
  border-radius: 50%;
  position: absolute;
  top: 100px;
  margin-left: 46%;
  margin-right: 40%;
  font-family: Poppins;
  font-weight: bold;
  display: flex;
  justify-content: center;
  align-items: center;
}

.headings {
  color: white;
  text-align: center;
  font-weight: normal;
  position: relative;
  margin: auto;
  font-size: 40px;
  font-weight: bold;
  top: -240px;
}

.locationSelector {
  position: relative;
  top: -238px;
  margin-left: 29.5%;
  margin-right: 20%;
  text-align: center;
}

.locationDropdown {
  height: 40px;
  width: 245px;
  color: gray;
  border: none;
  border-radius: 0px;
  font-size: 15px;
  opacity: 0.7;
  margin-right: 21px;
  margin-bottom: 25px;
  float: left;
}

.restaurantsinput {
  height: 40px;
  width: 310px;
  color: gray;
  border: none;
  border-radius: 0px;
  font-size: 15px;
  opacity: 0.7;
  float: left;
}


.search {
  position: absolute !important;
  left: 290px;
  margin: 2% 0px;
}

.quicksearch {
  position: absolute;
  margin-left: 15px;
  margin-top: -35px;
}
.lower-container{
  width: 100%;
  /* margin: 0 10em; */
  display: flex;
  align-items: center;
}


.quicksearchHeading {
  text-align: left;
  color: #192f60;
  font-size: 30px;
  font-weight: bold;
  font-family: Georgia, "Times New Roman", Times, serif;
  padding-left: 35px;
  margin-left: 30px;
}

.quicksearchSubHeading {
  text-align: left;
  color: gray;
  font-size: 18px;
  font-weight: normal;
  font-family: Georgia, "Times New Roman", Times, serif;
  padding-left: 35px;
  margin-left: 30px;
}

.tileContainer {
  height: 150px;
  width: 350px;
  background-color: white;
  margin: 0 70px 30px 70px;
  box-shadow: 0 3px 6px gray;
  float: left;
  transition: all 0.25s ease-in-out;
}

.tileContainer:hover{
  transform: scale(1.05);
  box-shadow: 0 0 13px #47474f;
}

.tileComponent1 {
  width: 45%;
  float: left;
}

.tileComponent2 {
  width: 50%;
  float: left;
  margin-top: 20px;
}

.componentHeading {
  text-align: left;
  color: #192f60;
  font-size: 18px;
  font-weight: bold;
  margin-bottom: 20px;
}

.componentSubHeading {
  text-align: left;
  color: gray;
  font-size: 18px;
}

.qs-block {
  box-shadow: 0 3px 6px 0 rgba(0, 0, 0, 0.16);
  background-color: #ffffff;
  margin-bottom: 5px;
}

.marg-bot {
  margin-bottom: 10px;
}

.heading {
  width: 88px;
  height: 25px;
  font-family: Poppins;
  font-size: 18px;
  font-weight: 600;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.5;
  letter-spacing: normal;
  text-align: left;
  color: #192f60;
}

.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}



@media only screen and (max-width: 600px) {
  .restaurantsinput {
      height: 40px;
      width: 245px;
      color: gray;
      border: none;
      border-radius: 0px;
      font-size: 15px;
      opacity: 0.7;
      float: left;
  }
  .logo {
      text-align: center;
      font-size: 65px;
      color: red;
      background: white;
      width: 90px;
      height: 95px;
      border-radius: 50%;
      position: absolute;
      top: 23%;
      margin-left: 40%;
      margin-right: 40%;
      font-family: Poppins;
      font-weight: bold;
  }
  .locationSelector {
      position: relative;
      top: -238px;
      margin-left: 23.5%;
      margin-right: 20%;
      text-align: center;
  }
  .search {
      position: absolute !important;
      left: 5px;
      margin: 2% 0px;
      top: 73px !important;
  }
  #notebooks {
      position: absolute;
      left: 28%;
      width: 350px;
      border-radius: 5px;
      max-height: 450px;

  }
}


@media only screen and (min-width: 600px) and (max-width: 1150px) {
  .locationDropdown {
      height: 40px;
      width: 310px;
      color: gray;
      border: none;
      border-radius: 0px;
      font-size: 15px;
      opacity: 0.7;
      margin-right: 21px;
      margin-bottom: 25px;
      float: left;
  }
  .locationSelector {
      position: relative;
      top: -238px;
      margin-left: 32.5%;
      margin-right: 20%;
      text-align: center;
  }
  .search {
      position: absolute !important;
      left: 5px;
      margin: 1% 0px;
      top: 73px !important;
  }
}


#notebooks {
  float: left;
}

#notebooks ul {
  margin: 0;
  padding: 0;
  margin-top: 40px;
  list-style: none;
}

#notebooks ul li {
  border: 1px solid #000;
  padding: 5px 10px;
  background-color: #ffffff;
  border-top: none;
  text-align: left;
  cursor: pointer;
}

#notebooks ul li:hover {
  background-color: rgb(234, 234, 234);
}

/* css for links dropdown */
.links{
  text-decoration: none;
  color: black;
}
.links:hover{
  color: black;
}

